html{font-family:'Kelly Slab', cursive}body{margin:0;padding:0 15px}h1{font-size:3em}h2{font-size:2em}h3{font-size:1.5em}.text-center{text-align:center}.homescreen{position:fixed;top:0;left:0;width:100vw;height:100vh;background:#fff;overflow-x:hidden;overflow-y:auto;padding:15px;transition:.3s}body.is-playing .homescreen{opacity:0;visibility:hidden;z-index:-1}.row{display:flex;align-items:center;flex-direction:row;justify-content:center}.role{padding:15px;text-align:center;width:170px}.role__name{margin-bottom:15px}.role__img{width:100px;height:100px;border:1px solid coral;border-radius:50%;margin:0 auto}.rules{max-width:600px;margin:0 auto;line-height:1.5}.rules b{text-decoration:underline}#playBtn{box-shadow:none;background:coral;border:none;padding:8px 24px;width:250px;font-size:2em;border-radius:8px;color:#fff;outline:none;margin:0 auto;display:block;cursor:pointer;transition:0.2s}#playBtn:hover{background:chocolate}#hh{width:400px;height:400px;background-image:url(assets/hh-stopped.gif);background-repeat:no-repeat;background-position:center;background-size:contain}
